:root {
  --color: #9176ff;
  --dark-color: #2b2b2b;
  --dark-icon-color: #fff;
  --light-color: #f7f7f7;
  --light-icon-color: #ffde59;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background-color: var(--light-color);
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn {
  width: 15em;
  height: 8em;
  cursor: pointer;
  background-color: #fff;
  border-radius: 10em;
  align-items: center;
  padding: 0 .5em;
  display: flex;
  position: relative;
  box-shadow: inset 0 8px 60px rgba(0, 0, 0, .1), inset 0 8px 8px rgba(0, 0, 0, .1), inset 0 -4px 4px rgba(0, 0, 0, .1);
}

.btn__indicator {
  width: 7em;
  height: 7em;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .3s;
  position: absolute;
  box-shadow: 0 8px 40px rgba(0, 0, 0, .2);
}

.btn__icon-container {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn__icon {
  color: var(--light-icon-color);
  font-size: 3rem;
}

.btn__icon.animated {
  animation: spin .5s;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

body.darkmode {
  background-color: var(--dark-color);
}

.darkmode .btn {
  box-shadow: inset 0 8px 60px rgba(0, 0, 0, .3), inset 8px 0 8px rgba(0, 0, 0, .3), inset 0 -4px 4px rgba(0, 0, 0, .3);
}

.darkmode .btn__indicator {
  background-color: var(--dark-color);
  transform: translateX(7em);
  box-shadow: 0 8px 40px rgba(0, 0, 0, .3);
}

.darkmode .btn__icon {
  color: var(--dark-icon-color);
}

